body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  font-size: 15px;
}

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid black;
}

table th,
table td {
  padding: 5px;
  border: 1px solid black;
  text-align: center;
}

table tr:hover {
  background-color: #ddd;
  /* Darker gray background on hover */
}

.header {
  text-align: center;
  padding: 20px;
  font-size: 24px;
  font-weight: bold;
}

input {
  width: 30%;
  padding: 5px 5px;
  margin: 8px 0;
  border: 2px solid black;
  border-radius: 4px;
  background-color: #f8f8f8;
  font-size: 16px;
}

input:focus {
  border: 2px solid #4CAF50;
  background-color: #e8f0fe;
}

input::placeholder {
  color: #999;
}

button {
  padding: 5px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: lightgray;
  /* Darker green on hover */
}

select {
  width: 30%;
  padding: 12px;
  /* Some padding */
  margin: 8px 0;
  /* Margin around */
  border: 2px solid #ccc;
  /* Gray border */
  border-radius: 4px;
  /* Rounded corners */
  background-color: #f8f8f8;
  /* Light gray background */
  font-size: 16px;
  /* Font size */
  color: #333;
  /* Text color */
}

select:focus {
  border: 2px solid green;
  /* Green border on focus */
  background-color: #e8f0fe;
  /* Light blue background on focus */
}